html
	font-size: 16px
	height: 100%
	+r(767)
		font-size: 14px

body
	font-family: "Open Sans"
	font-size: 12px
	line-height: 1.4
	position: relative
	margin: 0

	//padding-bottom: 6rem
	//min-height: 100%
$main: #00be7a
$text: #6c6969
$author-book: #949494
$title: #333
$bg: #f0f2f2
$grey: #d5d5d5

p
	+r(480)
		line-height: 24px !important

.h1
	color: $title
	+fs(40)
	font-weight: 300
	text-transform: uppercase
	+r(480)
		font-size: 28px

.h2
	color: $title
	+fs(36)
	font-weight: 400
	line-height: 30px
	text-align: center
	+r(480)
		font-size: 20px

.h3
	+fs(24)
	color: $title
	font-weight: 400
	line-height: 30px

p, .text
	+fs(14)
	color: $text
	line-height: 24px

a
	text-decoration: none
	&:hover
		//text-decoration: underline


// common blocks

.icon
	vertical-align: middle


.button
	background-color: $main
	border-radius: 50px
	padding: 15px 30px
	color: #fff
	+fs(18)
	box-shadow: 0 4px 10px rgba(55, 196, 112, 0.5)
	font-weight: 700
	display: inline-block
	//line-height: 13px
	+tr(all .2s)
	&:hover, &.is-active
		background-color: #14cf6c
		box-shadow: 0px 3px 22px 0px rgba(10, 214, 106, 0.42)
	+r(767)


.tr-button
	background-color: transparent
	border-radius: 50px
	padding: 15px 30px
	text-transform: uppercase
	font-family: "Open Sans"
	border: 0.063rem solid $main
	+tr(all .2s)
	&:hover, &.is-active
		background-color: $main
		color: white
		border-color: transparent

.wrapper
	overflow: hidden
	min-width: 320px
	//position: relative
	//padding-bottom: 233px

*
	-webkit-box-sizing: border-box
	-moz-box-sizing: border-box
	box-sizing: border-box



.gray-but
	border: 1px solid #c4c4c4
	text-transform: none !important
	font-size: 0.875rem

	

